<template>
  <div class="modal-main1">
    <el-form ref="form" :model="form" :rules="rules" label-width="17%" class="fromBox">
      <div class="rightBox">
        <el-form-item prop="name" label="选 择 机 构" >
          <el-select v-model="form.setMeal" placeholder="选择机构" ref="mySelect">           
          </el-select>
        </el-form-item>
        <el-form-item prop="" label="显 示 课 程" >
          <el-checkbox v-model="form.cur_state" @change="changeAllClass">显示所有课程</el-checkbox>          
          <el-input ref="allClaaInput" style="margin-left: 5%;"></el-input>
        </el-form-item>        
        <el-form-item prop="name" label="课 程 名 称" >
          <el-select v-model="form.setMeal" placeholder="选择课程" ref="mySelect">           
          </el-select>
        </el-form-item>
        <el-form-item prop="hour" label="添 加 学 生" class="short-item short-item2">
          <el-select v-model="form.timeType" placeholder="请添加学生">
            <el-option :value="0" :key="0" label="每周五"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" label="每两周五"></el-option>
          </el-select>
          <el-button class="xupai" type="text" @click="changeValue1($event,selectKey)">添加学生</el-button>
        </el-form-item>        
        <el-form-item prop="hour" label="上 课 时 间" class="short-item">
          <el-select v-model="form.timeType" placeholder="请选择" style="margin-right: 5%">
            <el-option :value="0" :key="0" label="每周五"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" label="每两周五"></el-option>
          </el-select>          
          <el-select v-model="form.hour" placeholder="时" class="noRight">
            <el-option value="0" label="00"></el-option>
            <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh gh1" type="text">时</el-button>
          <el-select v-model="form.minute" placeholder="分" class="minute noRight">
            <el-option :value="minute" label="00"></el-option>
            <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button>          
        </el-form-item>
        <el-form-item prop="hour" label="按 规 律 排" class="short-item short-item1">
          <el-select v-model="form.timeType" placeholder="请选择">
            <el-option :value="0" :key="0" label="每周五"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" label="每两周五"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button> 
          <el-button class="xupai" type="text" @click="changeValue($event,selectKey)">点击续排</el-button>
        </el-form-item>               
        <el-form-item prop="price" label="教 室 名 称" >
          <el-select v-model="form.setMeal" placeholder="请选择教室" ref="mySelect">           
          </el-select>
        </el-form-item>
        <el-form-item prop="remarks" label="备 注" >
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="可填写备注说明"
            maxlength="500"
            v-model="form.remarks">
          </el-input>
        </el-form-item>                                                                                                
      </div>                                             
    </el-form>    
  </div>
</template>
<script>
  import axios from 'axios';
  import {addmealcur,addmeals} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {checkMaxVal100,checkText} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
      that: {
        type: Object,
        default: false
      },                  
    },
    data() {
        return {
            status:'',
            tableData: [],
            minute:0,
            hour:0,
            form: {
              hours:null,
              minutes:null,
              timeType:null,              
              name: '',
              setMeal:'',
              value:'',
              price:'',
              cur_state:true,
              remarks:'',
            },
            rules: {
              name: [
              { required: true, message: '请输入套餐名称', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              {}
              ],
              setMeal: [
              { required: true}
              ],
              value: [
              { required: true}
              ],                                         
              price: [
              { required: true, message: '请输入套餐金额', trigger: 'blur' },
              { validator:checkMaxVal100, trigger: 'blur'},
              ],                                      
            },
            selectKey: 1,
            selectKey1: 1,
            optiopns:[{value: '2',label: '包时'}],
            meals_cur:'',
            Imageport          
        }
    },
    mounted() {
    },
    created(){
      
    },
    components:{
    	
    },
    methods: {
        hideModal() {
         this.$emit('hideModal');
        },        
        submit() {
          this.addmealcur()
        },
        appendzero:function(obj)
        {
          if(obj<10) return "0" +""+ obj;
          else return obj;
        },              
        async addmealcur() {
          if(!this.form.name){
            this.$message.error('请输入套餐名称');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          }
          else if(this.form.name.length < 2){
            this.$message.error('套餐名称长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          };          
          if(this.tableData.length == 0){
            this.$message.error('请选择套餐课程');
            this.$refs.mySelect.$el.children[0].children[0].click();
            return;            
          };
          let b = this.form.price;
          if(this.checkMaxVal1(b) == '1'){
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();
            return            
          }
          else if(this.checkMaxVal1(b) == '2'){
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();
            return            
          };                           
          console.log(this.tableData)
          let resDate = await addmealcur(this.tableData);
          let _this = this;
          if (resDate.data.status) {
          	resDate.data.data.forEach(function(element,index){
          		_this.meals_cur = _this.meals_cur + resDate.data.data[index] + '/';
          	});
          	this.addmeals() 
          }
        },
        changeAllClass(){
          console.log(this.$refs.allClaaInput)
          if(this.form.cur_state){
            this.$refs.allClaaInput.$el.style.display="block"
          }
          else{
            this.$refs.allClaaInput.$el.style.display="none"
          }
        },
		async addmeals() {
		  let meal_name = this.form.name
		  let value = this.form.value.toString()
		  let price = this.form.price
		  let meals_cur = this.meals_cur.substring(0,this.meals_cur.length-1)
		  let list_img = this.imageUrl1
		  let bg_img = this.imageUrl2
		  let cur_state
		  if(this.form.cur_state){
		  	cur_state = 1
		  }
		  else{
		  	cur_state = 2
		  }
		  let remarks = this.form.remarks
          let resDate = await addmeals(meal_name,value,price,cur_state,remarks,meals_cur,list_img,bg_img,)
          if (resDate.data.status) {
          	this.$message({
          		type: 'success',
          		message: resDate.data.sinfo
          	});          	
            this.$emit('submit')
          } 
        },        
        changeValue(event,b){
        	if(event && this.selectKey == '1'){
        		this.selectKey = 2;
        		this.showRight()
        		this.show1 = true;
        	}
          else{
            this.selectKey = 1;
            this.that.submitRight()
            this.show1 = false;            
          }
        },
        changeValue1(event,b){
          if(event && this.selectKey1 == '1'){
            this.selectKey1 = 2;
            this.showRight1()
          }
          else{
            this.selectKey1 = 1;
            this.that.submitRight1()           
          }
        },                
        showRight(){
          //console.log(this.that)
        	this.that.showRight()
        },
        showRight1(){
          this.that.showRight1()
        },        
        change_cur_value(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_value = 100
            this.$message.error('课程价值不能为0或者空')
          }          
        	var curValues = 0;
        	var _this = this;
        	// console.log(this.tableData)
        	this.tableData.forEach(function(element,index){
        		if(_this.tableData[index].cur_value){
        			curValues = curValues + parseFloat(_this.tableData[index].cur_value);
        		}
        	})
        	this.form.value = curValues;
        	if(this.form.price){
        		this.tableData.forEach(function(element,index){
        			_this.tableData[index].actual_price = (_this.form.price*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
        		})
        	}        	
        },
        change_cur_num(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_num = 10
            this.$message.error('课程节数不能为0或者空')
          }
        },
        focusPrice(){
          if(this.form.price){
            var actualPrice = this.form.price;
            if(typeof(actualPrice) != 'number'){
              actualPrice = actualPrice.split(".")[0];
              this.form.price = Number(actualPrice) 
            }
            else{
              this.form.price = actualPrice
            }            
          }       
        },                
        change_actual_price(){
        	var actualPrice = this.form.price;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('套餐金额范围在1-1000000')
            }                         
          }            
          var _this = this;
          this.tableData.forEach(function(element,index){
            if(_this.tableData[index].cur_value){
              _this.tableData[index].actual_price = (actualPrice*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
            }
          });
          this.form.price = actualPrice
        },
        focusPrice1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_value = e      
        },        
        change_actual_price1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('课程价值范围在0-1000000')
            }           
         }            
         this.tableData[index].cur_value = actualPrice 
        },
        focusPrice2(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_num = e      
        },        
        change_actual_price2(e,index){
          console.log(index)
          var actualPrice = e;
          console.log(typeof(actualPrice))
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 100){
              actualPrice=100
              this.$message.error('课程节数范围在1-100')
            }           
         }            
         this.tableData[index].cur_num = actualPrice 
        },                 
       checkMaxVal1(value) {
        var v = value
        if (isNaN(value) || typeof(v) != 'number' || v <= 0 || v > 1000000) {
            return 1;//'请输入[1,500]之间的整数'
          } else {
            const re = /^[0-9]*[1-9][0-9]*$/;
            const rsCheck = re.test(value);
            if (!rsCheck) {
              return 2;//'请输入正整数'
            } else {
              return 0;
            } 
          }
        },       	          
    }
  }
</script>
