<template>
  <div class="modal-main1">
    <el-form ref="form" :model="form" :rules="rules" label-width="17%" class="fromBox">
      <div class="rightBox">
        <el-form-item prop="orgid" label="选 择 机 构" >
          <el-select v-model="form.orgid" placeholder="选择机构" :disabled="true">
            <el-option :value="that.that.userLoginInfo.orgid" :key="that.that.userLoginInfo.orgid" :label="that.that.userLoginInfo.config.name"></el-option>           
          </el-select>
        </el-form-item>
        <el-form-item prop="stu_id" label="选 择 学 生" >
          <el-select v-model="form.stu_id" placeholder="请选择学生" ref="stu_id_Box"> 
            <el-option v-for="(item,index) in studentList" :value="item.stu_id" :label="item.stu_name" :key="item.stu_id"></el-option>          
          </el-select>
        </el-form-item> 
        <el-form-item prop="currm" label="课 程 名 称">
          <el-select v-model="form.currm" placeholder="选择课程" ref="currmBox">
            <el-option-group
              v-for="group in currm_list"
              :key="group.sid"
              :label="group.sname">
              <el-option v-for="(item,index) in group.courses" :value="item.cur_id" :label="item.cur_name" :key="item.cur_id"></el-option>
            </el-option-group>                    
          </el-select>
        </el-form-item>
        <el-form-item prop="timeType" label="上 课 时 间" class="short-item">
          <el-select v-model="form.timeType" placeholder="请选择" style="margin-right: 5%" ref="hourBox">
            <el-option :value="0" :key="0" :label="'每周'+today"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" :label="'每两周'+today"></el-option>
          </el-select>          
          <el-select v-model="form.hours" placeholder="时" class="noRight" ref="hoursBox">
            <el-option value="0" label="00"></el-option>
            <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh gh1" type="text">时</el-button>
          <el-select v-model="form.minutes" placeholder="分" class="minute noRight" ref="minutesBox">
            <el-option :value="minute" label="00"></el-option>
            <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button>          
        </el-form-item>
        <el-form-item prop="law" label="按 规 律 排" class="short-item short-item1">
          <el-select v-model="form.law" placeholder="请选择" class="noRight" ref="lawBox">
            <el-option v-for="i in 100" :value="i" :label="i" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">节课</el-button>
          <el-button class="xupai" type="text" @click="changeValue($event,selectKey)">点击续排</el-button>
        </el-form-item>               
        <el-form-item prop="c_id" label="教 室 名 称" >
          <el-select v-model="form.c_id" placeholder="请选择教室" ref="c_id_box">
            <el-option v-for="(item,index) in roomList" :value="item.room_id" :label="item.room_name" :key="item.room_id"></el-option>           
          </el-select>
        </el-form-item>
        <el-form-item prop="remarks" label="备 注" >
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="可填写备注说明"
            maxlength="500"
            v-model="form.remarks">
          </el-input>
        </el-form-item>                                                                                                
      </div>                                             
    </el-form>    
  </div>
</template>
<script>
  import axios from 'axios';
  import {add_teacher_schedule,all_students,all_list_currm,selectedRoom} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {checkMaxVal100,checkText} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
      that: {
        type: Object,
        default: false
      },                  
    },
    data() {
        return {
            status:'',
            studentList: [],
            currm_list:[],
            roomList:[],
            minute:0,
            hour:0,
            today:'',
            c_type:1,//一对一
            form: {
              hours:7,
              minutes:30,
              timeType:0,              
              orgid:this.that.that.userLoginInfo.orgid,
              stu_id:null,
              currm:null,
              law:null,
              c_id:null,
              remarks:'',
            },
            rules: {
              orgid: [
              { required: true,},
              ],
              stu_id: [
              { required: true}
              ],
              currm: [
              { required: true}
              ],
              c_id: [
              { required: true}
              ],
              timeType: [
              { required: true}
              ],
              law: [
              { required: true}
              ],                                                                     
              price: [
              { required: true, message: '请输入套餐金额', trigger: 'blur' },
              { validator:checkMaxVal100, trigger: 'blur'},
              ],                                      
            },
            selectKey: 1,
            Imageport          
        }
    },
    mounted() {
      let tds = this.that.clickId
      if(tds == 0){
        this.today = '日'
      }
      else if(tds == 1){
        this.today = '一'
      }
      else if(tds == 2){
        this.today = '二'
      }      
      else if(tds == 3){
        this.today = '三'
      }      
      else if(tds == 4){
        this.today = '四'
      }      
      else if(tds == 5){
        this.today = '五'
      }      
      else if(tds == 6){
        this.today = '六'
      }            
    },
    created(){
      this.all_students()
      this.all_list_currm()
      this.selectedRoom()
    },
    components:{
    	
    },
    methods: {
        appendzero:function(obj)
        {
          if(obj<10) return "0" +""+ obj;
          else return obj;
        },
        async all_students () {  
          let resDate = await all_students(this.that.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.studentList = resDate.data.data        
          }
        },
        async all_list_currm () {  
          let resDate = await all_list_currm(this.that.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.currm_list = resDate.data.data        
          }
        },
        async selectedRoom () {  
          let resDate = await selectedRoom(this.that.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.roomList = resDate.data.data        
          }
        },                                    
        async add_teacher_schedule() {
          if(!this.form.stu_id){
            this.$message.error('请选择学生');
            this.$refs.stu_id_Box.$el.children[0].children[0].click();
            return;            
          }
          else if(!this.form.currm){
            this.$message.error('你选择课程');
            this.$refs.currmBox.$el.children[0].children[0].click();
            return;            
          }
          else if(!this.form.law){
            this.$message.error('请选择排课规律');
            this.$refs.lawBox.$el.children[0].children[0].click();
            return;            
          }          
          else if(!this.form.c_id){
            this.$message.error('请选择教室');
            this.$refs.c_id_box.$el.children[0].children[0].click();
            return;            
          };
          let day =  this.that.clickId;
          let toDays = new Date(this.that.toDays + ' ' + this.appendzero(this.form.hours) + ':' + this.appendzero(this.form.minutes) + ':' + '00').getTime()/1000
          let today =  new Date(this.that.toDays).getTime()/1000                                                   
          let resDate = await add_teacher_schedule(this.form.stu_id,this.form.currm,this.that.that.teacherId,this.form.timeType,this.form.law,this.form.c_id,this.form.remarks,day,toDays,today,this.c_type);
          let _this = this;
          if (resDate.data.status) {
          	 this.that.$emit('hideModal');
          }
        },       
        changeValue(event,b){
        	if(event && this.selectKey == '1'){
        		this.selectKey = 2;
        		this.showRight()
        		this.show1 = true;
        	}
          else{
            this.selectKey = 1;
            this.that.submitRight()
            this.show1 = false;            
          }
        },        
        showRight(){
          console.log(this.that)
        	this.that.showRight()
        },
        change_cur_value(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_value = 100
            this.$message.error('课程价值不能为0或者空')
          }          
        	var curValues = 0;
        	var _this = this;
        	// console.log(this.tableData)
        	this.tableData.forEach(function(element,index){
        		if(_this.tableData[index].cur_value){
        			curValues = curValues + parseFloat(_this.tableData[index].cur_value);
        		}
        	})
        	this.form.value = curValues;
        	if(this.form.price){
        		this.tableData.forEach(function(element,index){
        			_this.tableData[index].actual_price = (_this.form.price*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
        		})
        	}        	
        },
        change_cur_num(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_num = 10
            this.$message.error('课程节数不能为0或者空')
          }
        },
        focusPrice(){
          if(this.form.price){
            var actualPrice = this.form.price;
            if(typeof(actualPrice) != 'number'){
              actualPrice = actualPrice.split(".")[0];
              this.form.price = Number(actualPrice) 
            }
            else{
              this.form.price = actualPrice
            }            
          }       
        },                
        change_actual_price(){
        	var actualPrice = this.form.price;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('套餐金额范围在1-1000000')
            }                         
          }            
          var _this = this;
          this.tableData.forEach(function(element,index){
            if(_this.tableData[index].cur_value){
              _this.tableData[index].actual_price = (actualPrice*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
            }
          });
          this.form.price = actualPrice
        },
        focusPrice1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_value = e      
        },        
        change_actual_price1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('课程价值范围在0-1000000')
            }           
         }            
         this.tableData[index].cur_value = actualPrice 
        },
        focusPrice2(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_num = e      
        },        
        change_actual_price2(e,index){
          console.log(index)
          var actualPrice = e;
          console.log(typeof(actualPrice))
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 100){
              actualPrice=100
              this.$message.error('课程节数范围在1-100')
            }           
         }            
         this.tableData[index].cur_num = actualPrice 
        },                 
       checkMaxVal1(value) {
        var v = value
        if (isNaN(value) || typeof(v) != 'number' || v <= 0 || v > 1000000) {
            return 1;//'请输入[1,500]之间的整数'
          } else {
            const re = /^[0-9]*[1-9][0-9]*$/;
            const rsCheck = re.test(value);
            if (!rsCheck) {
              return 2;//'请输入正整数'
            } else {
              return 0;
            } 
          }
        },       	          
    }
  }
</script>
